.main{
    width: 100%;
}
.sectitle{
    position: relative;
    width: max-content;
    margin: 1rem 0;
}
.sectitle {
    position: relative;
    
    margin: 1rem 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
.singleDestination {
    background-color: #fff;
    border: 1px solid #E7E6E6;
    padding: 10px;
    margin-bottom: 10px;
    border-radius: 12px;
}
.contant {
    padding: 10px;
}
.flex.conf {
    justify-content: space-between;
}
h3.title {
    font-size: 20px;
    margin: 10px 0px;
}
.conf>span:nth-child(2){
    font-size: 21px ;
    font-weight: 700;
}
@media screen and (min-width:500px) {
    .section-con{
        grid-template-columns: repeat(2, 1fr);
    }
    .singleDestination {
        height: auto;
        margin: 10px;
    }

}
@media screen and (min-width:824x) {
    .section-con{
        grid-template-columns: repeat(3, 1fr);
    }
    .singleDestination {
        height: auto;
        margin: 10px;
    }

}
p.con {
    margin-bottom: 10px;
}
@media screen and (min-width:800px) {
    .section-con{
        grid-template-columns: repeat(4, 1fr);
    }
    .singleDestination {
        height: auto;
        margin: 10px;
    }

}